<template>
    <div class="header-wrapper">
      <div class="content-wrapper">
        <div class="avatar">
          <img :src="seller.avatar" alt="" width="64" height="64">
        </div>
        <div class="content">
          <div class="title">
            <span class="brand"></span>
            <span class="name">{{seller.name}}</span>
          </div>
          <div class="description">
            {{seller.description}}/{{seller.deliveryTime}}分钟送达
          </div>
          <div v-if="seller.supports" class="support">
            <span class="icon" :class="classMap[seller.supports[0].type]"></span>
            <span class="text">{{seller.supports[0].description}}</span>
          </div>
        </div>
        <div v-if="seller.supports" class="support-count" @click="showDetail">
          <span class="count">{{seller.supports.length}}个</span>
          <i class="icon-keyboard_arrow_right "></i>
        </div>
      </div>
      <div class="bulletin-wrapper">
        <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
        <i class="icon-keyboard_arrow_right" @click="showDetail" ></i>
      </div>
      <div class="background">
        <img :src="seller.avatar" alt="">
      </div>
      <transition name="fade">
        <div v-show="detailShow" class="detail">
          <div class="detail-wrapper clearfix">
            <div class="detail-main">
              <h1 class="name">{{seller.name}}</h1>
              <div class="star-wrapper">
                <v-star :size="48" :score="seller.score"></v-star>
              </div>
              <div class="title">
                <div class="line"></div>
                <div class="text">优惠信息</div>
                <div class="line"></div>
              </div>
              <ul v-if="seller.supports" class="supports">
                <li class="support-item" v-for="(item,index) in seller.supports">
                  <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                  <span class="text">{{seller.supports[index].description}}</span>
                </li>
              </ul>
              <div class="title">
                <div class="line"></div>
                <div class="text">商家公告</div>
                <div class="line"></div>
              </div>
              <div class="bulletin">
                <p class="content">{{seller.bulletin}}</p>
              </div>
            </div>
          </div>
          <div class="detail-close" @click="hideDetail">
            <i class="icon-close"></i>
          </div>
        </div>
      </transition>
    </div>
</template>

<script type="text/ecmascript-6">
  import star from 'components/star/star.vue';
  console.dir(star);

    export default {
      name: 'header',
      props: {
        seller: {
          type: Object
        }
      },
      data() {
        return {
          detailShow: false
        };
      },
      methods: {
        showDetail() {
          this.detailShow = true;
        },
        hideDetail() {
          this.detailShow = false;
        }
      },
      created() {
        this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
      },
      components: {
        'v-star': star
      }
    };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"

    .header-wrapper
      position relative
      color #ffffff
      overflow hidden
      background rgba(7,17,27,0.5)
      .content-wrapper
        position relative
        padding 24px 12px 18px 24px
        font-size 0
        .avatar
          display inline-block
          vertical-align top
          img
            border-radius 2px
        .content
          display inline-block
          vertical-align top
          font-size 14px
          margin-left 16px
          .title
            margin 2px 0 8px 0
            .brand
              display inline-block
              vertical-align top
              width 30px
              height 18px
              bg-img('./images/brand')
              background-size 30px 18px
              background-repeat no-repeat
            .name
              vertical-align top
              margin-left 6px
              line-height 18px
              font-size 16px
              font-weight bold
          .description
            margin-bottom 10px
            line-height 12px
            font-size 12px
          .support
            .icon
              display inline-block
              vertical-align middle
              width 12px
              height 12px
              margin-right 4px
              bg-img('./images/decrease_1')
              background-size 12px 12px
              background-repeat no-repeat
            .text
              line-height 12px
              font-size 10px
        .support-count
          position absolute
          right 12px
          bottom 14px
          padding 0 8px
          height 24px
          line-height 24px
          border-radius 14px
          background rgba(0,0,0,0.2)
          text-align center
          .count
            margin-left 2px
            font-size 10px
            vertical-align middle
          .icon-keyboard_arrow_right
            font-size 10px
            vertical-align middle
            margin-left 2px
      .bulletin-wrapper
        position relative
        height 28px
        line-height 28px
        padding 0 22px 0 12px
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
        background rgba(7,17,27,0.2)
        .bulletin-title
          display inline-block
          vertical-align middle
          width 22px
          height 12px
          bg-img('./images/bulletin')
          background-size 22px 12px
          background-repeat no-repeat
        .bulletin-text
          font-size 10px
          vertical-align middle
          margin 4px
        .icon-keyboard_arrow_right
          position absolute
          font-size 10px
          right 12px
          bottom 8px
      .background
        position absolute
        top 0
        left 0
        z-index -1
        width 100%
        height 100%
        filter blur(10px)
        img
          width 100%
      .detail
        position fixed
        top 0
        left 0
        z-index 100
        width 100%
        height 100%
        overflow auto
        background rgba(7,17,27,0.8)
        backdrop-filter blur(10px)
        &.fade-enter-active, &.fade-leave-active
          transition opacity 1s
        &.fade-enter, &.fade-leave-active
          opacity 0
        .detail-wrapper
          width 100%
          min-height 100%
          .detail-main
            margin-top 64px
            padding-bottom 74px
            .name
              line-height 16px
              text-align center
              font-size 16px
              font-weight 700
            .star-wrapper
              margin-top 18px
              padding 2px 0
              text-align center
            .title
              display flex
              width 80%
              margin 28px auto 24px auto
              .text
                padding 0 12px
                font-size 14px
                font-weight 700
              .line
                flex 1
                position relative
                top -6px
                border-bottom 1px solid rgba(255,255,255,0.2)
            .supports
              width 80%
              margin 0 auto
              .support-item
                padding 0 12px
                margin-bottom 12px
                font-size 0
                &:last-child
                  margin-bottom 0
                .icon
                  display inline-block
                  width 16px
                  height 16px
                  vertical-align middle
                  margin-right 6px
                  background-size 16px 16px
                  background-repeat no-repeat
                  &.decrease
                    bg-img('./images/decrease_1')
                  &.discount
                    bg-img('./images/discount_1')
                  &.guarantee
                    bg-img('./images/guarantee_1')
                  &.invoice
                    bg-img('./images/invoice_1')
                  &.special
                    bg-img('./images/special_1')
                .text
                  margin 0 12px
                  font-size 10px
                  vertical-align middle
            .bulletin
              width 80%
              margin 0 auto
              .content
                padding 0 12px
                line-height 24px
                font-size 12px
        .detail-close
          position relative
          width 32px
          height 32px
          margin -64px auto 0 auto
          font-size 32px
</style>



